<template>
	<div class="device-page">
		<div class="device-header">
			<div class="device-header-back" @click="$router.back()">
				<i class="fa fa-angle-left fa-2x"></i>
				<span>{{$t('pageCommon.back')}}</span>
			</div>
			<div>
				<button class="add-btn" @click="createDeviceCtrl=true">{{$t('devicePage.addDevice')}}</button>
			</div>
		</div>
		<el-table
			:data="tableData"
			stripe
			class='device-table'>
			<el-table-column
				prop="num"
				:label="$t('devicePage.prop.num')"
				width="104">			
			</el-table-column>
			<el-table-column
				prop="type"
				:label="$t('devicePage.prop.type')">
			</el-table-column>			
			<el-table-column
				prop="mac"
				:label="$t('devicePage.prop.mac')">
			</el-table-column>			
			<el-table-column
				prop="userName"
				:label="$t('devicePage.prop.user')">
			</el-table-column>
            <el-table-column
                :label="$t('devicePage.prop.Management')"
                width="160">
                <template scope="scope">
                    <!--<el-button class='device-table-editor'>{{$t('pageCommon.editorText')}}</el-button>-->
					<el-button class='device-table-delete' @click="deleteRow(scope.row)">{{$t('pageCommon.deleteText')}}</el-button>
                </template>
            </el-table-column>
		</el-table>
		<el-pagination
			mini
			layout="prev, pager, next"
			:current-page.sync="currentPage"
			:page-size='pageSize'
			:total="counts" @current-change="currentPageChange"
			class='device-pagination' v-if="counts>30">
		</el-pagination>		
	    <transition name="addDevice">
	      	<div class="device-shade" v-if="createDeviceCtrl" @click.stop="createDeviceCtrl=false">
		      	<div class="device-window" @click.stop="createDeviceCtrl=true">
		        	<div class="add-device-top">
		          		<span>{{$t('devicePage.form.addUser')}}</span>
		          		<i class="fa fa-close" @click.stop="createDeviceCtrl=false"></i>
		        	</div>
		        	<el-form :model="deviceMsgList" ref="deviceMsgList" :rules='deviceRules'>
			        	<div class="add-device-info">
			            	<p>{{$t('devicePage.form.type')}}</p>
							<el-form-item prop="type">
								<el-input type="text" v-model='deviceMsgList.type' auto-complete="off" :placeholder="$t('devicePage.form.typeText')"></el-input>
							</el-form-item>	
			            	<p>{{$t('devicePage.form.userName')}}</p>
							<el-form-item prop="name">
								<el-input type="text" v-model="deviceMsgList.name" auto-complete="off" :placeholder="$t('devicePage.form.user')"></el-input>
							</el-form-item>	
			            	<p>{{$t('devicePage.form.mac')}}</p>
							<el-form-item prop="mac">
								<el-input type="text" v-model="deviceMsgList.mac" auto-complete="off" :placeholder="$t('devicePage.form.macText')"></el-input>
							</el-form-item>	
			        	</div>
			        	<div class="add-device-btn">
						<el-form-item>
					    	<el-button @click="createDevice('deviceMsgList')" class='device-create-btn'>{{$t('devicePage.add')}}</el-button>
						</el-form-item>			          		
			        	</div>
			        </el-form>
		      	</div>	      	
	      	</div>
	    </transition>
	</div>
</template>

<script>
	import './device.scss'
    export default {
        name:'Device',
		data() {
	    	// 验证Mac地址
	      	var checkMac = (rule, value, callback) => {
		        if (!value) {
		          	return callback(new Error(this.$t('devicePage.form.noMac')));
		        }
		        setTimeout(() => {
			        if (!(/^[A-Fa-f\d]{2}:[A-Fa-f\d]{2}:[A-Fa-f\d]{2}:[A-Fa-f\d]{2}:[A-Fa-f\d]{2}:[A-Fa-f\d]{2}$/.test(value))) {
			            callback(new Error(this.$t('devicePage.form.checkMac')));
			        }else{
		              	callback();
		           }
		        }, 1000);
	    	}; 			
			return {
                pageSize : 30,
                currentPage: 1,
                counts: 0,				
				createDeviceCtrl : false,
				tableData: [],
		        ReqList: {
		          	where: {
		              	offset: 0,
		              	limit: 30,
		              	sortBy: 'createdAt',
		              	order: 1
		            }
		        },
		        deviceMsgList : {
		        	type : '',
		        	name : '',
		        	mac : ''
		        },
		        deviceRules: {
					mac : [
						{ validator: checkMac, trigger: 'blur' }
					]
				}		        
			}
		},
		methods : {
			getDeviceData(){
	      		this.$http.GetGroupsDeviceList(this.$route.params.id, this.ReqList)
	      		.then((data)=>{
	      			let tempTableData = []
	      			for(let i = 0; i < data.data.list.length; i++){
	      				tempTableData.push({
	      					num : (i + 1) + (this.currentPage - 1) * 30,
	      					type : data.data.list[i].child.model,
	      					mac : data.data.list[i].child.mac,
	      					userName : data.data.list[i].child.name,
	      					childId : data.data.list[i]._id
	      				})	
	      			}
	      			this.tableData = tempTableData
	      			this.counts = data.data.count
	      		})
	      		.catch(error=>{
	      			console.log(error)
	      		})
			},
			createDevice(formName){
		        this.$refs[formName].validate((valid) => {
		        	if (!valid) {
				        this.$message({message : this.$t('messageTip.addFail'), duration : 2000, type: 'error'})
				        return false;	            	
		        	}
					this.$http.AddDeviceToGroups(this.$route.params.id, this.deviceMsgList)
					.then(data=>{
						this.$message({message : this.$t('messageTip.addSuccess'), duration : 2000, type: 'success'})
						this.getDeviceData()
					})		        	
		        	.catch(()=>{
		        		this.$message({message : this.$t('messageTip.addFail'), duration : 2000, type: 'error'})
		        	})
		        	
		      	});
		   },
		   deleteRow(row){
		        this.$confirm(this.$t('messageTip.msgBox.confirm'), this.$t('messageTip.msgBox.tip'), {
		            confirmButtonText: this.$t('messageTip.msgBox.confirmText'),
		            cancelButtonText: this.$t('messageTip.msgBox.cancelText'),
		            type: 'warning'
		        }).then(() => {
					this.$http.DeleteGroupsDevices(this.$route.params.id, row.childId)
						.then(data=>{
                        	let tempoffset = this.ReqList.where.offset
                        	let tempcounttwo = (this.counts) / tempoffset
                        	let tempcountone = (this.counts - 1) / tempoffset
                        	if(tempoffset !== 0 && (Math.ceil(tempcounttwo) === this.currentPage) && ((tempcountone) % 1 === 0)){
                        		this.currentPage -= 1
                        	}else{
                        		this.getDeviceData()
                        	}							
						})
		            this.$message({
		              type: 'success',
		              message: this.$t('messageTip.deleteSuccess')
		            });		        	
		        }).catch(() => {
		        	this.$message({
		        	  	type: 'info',
		        	  	message: this.$t('messageTip.cancelDelete')
		        	});		        			             
		        });		   		
		   	},
			//	分页
			currentPageChange(val){
                this.ReqList.where.offset = (val - 1) * 30
                this.getDeviceData()
			}
		},
		created(){
      		this.getDeviceData()
		}		
    }	
</script>


